<template>
  <div class="login flex column ac">
    <img class="login-img" :src="require('@/assets/login.png')" alt="">
    <div class="btn">
      <div class="wx flex ac center" @click="wxAuth">
        <img :src="require('@/assets/wx.png')" alt /> 微信登录
      </div>
      <div class="other text-center" @click="toPath('/login/account')">其它方式登录</div>
    </div>
  </div>
</template>

<script>
import { getWxCodeToAccess, wxLogin } from "@/api";
import { wxAuth } from '@/utils/wx'

export default {
  data() {
    return {};
  },
  created() {
    this.wxLogin()
  },
  methods: {
    toPath(path) {
      this.$router.replace(path);
    },
    wxAuth() {
      wxAuth()
    },
    async wxLogin() {
      const { code } = this.$route.query;
      if (code) {
        // 获取access-token
        const result = await getWxCodeToAccess({ code })
        if (result.success) {
          console.log('getWxCodeToAccess：', result)
          const { openid, unionid } = result.data
          const params = {
            type: 2,
            openid,
            unionid,
            login_platform: 'h5'
          }
          // 微信登录
          const result2 = await wxLogin(params)
          console.log('wxLogin', result2)
          if(result2.success) {
            if (!result2.data.token) { // 未关联手机号
              this.$toast('该微信号未绑定相关账号，请使用手机号登录')
              setTimeout(() => {
                this.$router.replace('/login/account')
              }, 2000)
              return
            }
            localStorage.setItem("token", result2.data.token);
            localStorage.setItem("user_id", result2.data.user_id);
            localStorage.setItem("userInfo", JSON.stringify(result2.data));
            this.$router.replace("/home");
          }
        }
      }
    }
  }
};
</script>

<style lang="less" scoped>
.login {
  height: 100%;
  box-sizing: border-box;
  background: #fff;
  overflow: hidden;
}

.login-img{
  width: 349px;
  margin-top: 58px;
  margin-bottom: 40px;
}

.btn {
  width: 100vw;
}

.wx {
  width: 321px;
  height: 46px;
  background: #06C561;
  border-radius: 24px;
  box-sizing: border-box;
  color: #fff;
  font-size: 18px;
  margin: 0 auto 15px;
  img {
    width: 31px;
    margin-right: 10px;
  }
}

.other {
  color: #666;
  font-size: 14px;
  line-height: 20px;
}
</style>
